.btn-primary {
  background-color: #de3473;
  color: #ffffff;
}
.btn-primary:hover {
  background-color: #c12d64;
  border-color: #c12d64;
  color: #ffffff;
  box-shadow: none;
}
.btn-secondary {
  background-color: #ff9000;
  color: #ffffff;
}
.btn-secondary:hover {
  background-color: #ff6c00;
  border-color: #ff6c00;
  color: #ffffff;
  box-shadow: none;
}
.btn-skeleton {
  background-color: #ffffff;
  color: #757575;
  border-color: #bdbcbd;
}
.btn-skeleton:hover {
  background-color: #ffffff;
  color: #525252;
  border-color: #525252;
  box-shadow: none;
}
.btn-link {
  text-decoration: underline;
}
.btn-link:hover {
  text-decoration: none;
}
.btn-blank {
  padding: 0;
  border: 0;
  background-color: transparent;
}
.btn-blank:active {
  box-shadow: none;
}
.btn-green {
  background-color: #006027;
  color: #ffffff;
}
.btn-green:hover {
  background-color: #008335;
  border-color: #008335;
  color: #ffffff;
  box-shadow: none;
}
.btn-green:focus,
.btn-green.focus {
  color: #ffffff;
}
